<template>
  <div id="checkout">
    <h1>購物車</h1>
    <div class="checkout__container">
      <ul class="checkout__steps">
        <li class="active">
          <div class="rwd--m">
            1
          </div>
          <div class="rwd--l">
            確認商品數量
          </div>
        </li>
        <li>
          <div class="rwd--m">
            2
          </div>
          <div class="rwd--l">
            填寫訂購資料
          </div>
        </li>
        <li>
          <div class="rwd--m">
            3
          </div>
          <div class="rwd--l">
            確認訂單明細
          </div>
        </li>
        <li>
          <div class="rwd--m">
            4
          </div>
          <div class="rwd--l">
            選擇付款方式
          </div>
        </li>
        <li>
          <div class="rwd--m">
            5
          </div>
          <div class="rwd--l">
            完成訂購
          </div>
        </li>
      </ul>
    </div>
    <router-view
    :cart='cart'
    :user='user'
    :orders='orders'
    :this_order='this_order'
    :coupons='coupons'
    ></router-view>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  props: ['cart', 'user', 'orders', 'coupons'],
  data() {
    return {
      this_order: {
        id: '',
        user: {
          id: '',
        },
        cart: {},
        coupon: {
          coupon_id: '無',
          coupon_discount_method: '',
          coupon_discount_index: 0,
          coupon_discount: 0,
        },
        sum: 0,
        buyer: {
          name: '',
          tel: '',
          email: '',
        },
        invoice: {
          type: 'personal_invoice',
          carrier: 'no_carrier',
          address: {
            county: '',
            district: '',
            zip_code: '',
            full_address: '',
          },
        },
        recipient: {
          takeaway: false,
          name: '',
          tel: '',
          address: {
            county: '',
            district: '',
            zip_code: '',
            full_address: '',
          },
          recipient_alternative: {
            available: false,
            name: '',
          },
          anonymous: false,
          estimated_date: '',
          estimated_time: '',
          card_content: '',
          order_remark: '',
        },
        payment: {
          status: 'not_yet',
          method: '',
          credit_card_number: '',
          credit_card_expMonth: '',
          credit_card_expYear: '',
          credit_card_cvc: '',
        },
      },
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    update_step(step) {
      const activeEl = [...$('.checkout__steps li')]
        .find((item) => [...item.classList].includes('active'));
      const thisEl = [...$('.checkout__steps li')][step - 1];
      $(activeEl).removeClass('active');
      $(thisEl).addClass('active');
      [...$('.checkout__steps li')].forEach((item) => {
        $(item).removeClass('completed');
      });
      for (let index = 0; index < step - 1; index += 1) {
        const completedEl = [...$('.checkout__steps li')][index];
        $(completedEl).addClass('completed');
      }
    },
  },
  created() {
    this.cart.is_shown = false;
  },
  mounted() {
    this.$bus.$on('update_step', (index) => {
      this.update_step(index);
    });
  },
};
</script>
